<template>
  <div>
    <h4 class="ms-4">Profile</h4>
    <v-tabs v-model="tab">
            <v-tab cols="6">GENERAL</v-tab>
            <v-tab cols="6">CHANGE PASSWORD</v-tab>
          </v-tabs>
    <v-tabs-items v-model="tab">
            <v-tab-item class="mt-4">
              <v-card color="basil" flat >
                <v-form>
                  <v-row>
                    <v-col cols="12" md="12" class="py-0">
                      <v-avatar  size="100">
                        <img :src="form1.avatar_media_url" alt="John" @click="pickFile('avatar')">
                      </v-avatar>
                      <input type="file" style="display: none" id="avatar" name="avatar" accept="image/*" @change="onFilePicked">
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-text-field label="Name" required ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-text-field label="E-mail address" required ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-text-field label="Enter Mobile" required ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-text-field label="Reseller ID" required ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-text-field label="GST" required ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-select :items="items1" label="Select Gender" dense></v-select>
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-autocomplete label="Select Your Profession"
                                      :items="allProfessions" item-text="profession" item-value="id" prepend-inner-icon="mdi-tray-arrow-down"
                      ></v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-select :items="allSkills" :menu-props="{ maxHeight: '400' }" prepend-inner-icon="mdi-code-tags"
                                label="Skills & Experience" multiple persistent-hint
                      ></v-select>
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-autocomplete label="Select State"
                                      :items="allstates" item-text="state" item-value="id" prepend-inner-icon="mdi-city"
                      ></v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-autocomplete label="Select City"
                                      :items="allcities" item-text="city" item-value="id" prepend-inner-icon="mdi-home-city"
                      ></v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="12" class="py-0">
                      <v-textarea rows="3" row-height="20" label="Select Address"></v-textarea>
                    </v-col>
                  </v-row>
                  <div class="text-end">
                    <v-btn class="my-2 ms-4 cred">UPDATE</v-btn>
                  </div>
                </v-form>
              </v-card>
            </v-tab-item>
            <v-tab-item >
              <v-card color="basil" flat >
                <v-form>
                  <v-row>
                    <v-col class="py-2" cols="12" md="12">
                      <v-text-field v-model="password" :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                    :rules="[rules.required, rules.min]" :type="show1 ? 'text' : 'password'" name="input-10-1"
                                    label="Password"
                                    hint="At least 8 characters"
                                    @click:append="show1 = !show1"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <div class="text-end">
                    <v-btn class="my-3 ms-4 cred">UPDATE</v-btn>
                  </div>
                </v-form>
              </v-card>
            </v-tab-item>
          </v-tabs-items>
  </div>
</template>
<script>

export default {
  name: 'ResellerProfile',
  components: {},
  metaInfo: {title: 'Reseller Profile'},
  data() {
    return {
      allstates:[],
      allcities:[],
      allProfessions:[],
      allSkills:[],
      items1: ['Male', 'Female'],
      items2: ['Foo', 'Bar', 'Fizz', 'Buzz'],
      items3: ['Foo', 'Bar', 'Fizz', 'Buzz'],
      password: '',
      show1: false,
      tab: '',
      activeFilePickerId: null,
      showDeleteAvatar: false,
      rules: {
        required: value => !!value || 'Required.',
        min: v => v.length >= 8 || 'Min 8 characters',
      },
      form1: {
        avatar_media_url: 'https://bizlx.itechvision.in/images/hot-deal-home-2.jpg',
        avatar_media_changed: false,
      },
    }
  },
  methods:{
    pickFile (id) {
      this.activeFilePickerId = id
      document.getElementById(id).click();
    },
    onFilePicked (e) {
      const files = e.target.files
      if(files[0] !== undefined) {
        if(files[0].name.lastIndexOf('.') <= 0) {
          return
        }
        const fr = new FileReader ()
        fr.readAsDataURL(files[0])
        fr.addEventListener('load', () => {
          this.form1[this.activeFilePickerId + '_media_url'] = fr.result
          this.form1[this.activeFilePickerId + '_media_file'] = files[0]
          this.form1[this.activeFilePickerId + '_media_changed'] = true
          this.showDeleteAvatar = true
        })
      } else {
        this.form1[this.activeFilePickerId + '_media_file'] = ''
        this.form1[this.activeFilePickerId + '_media_url'] = ''
        this.form1[this.activeFilePickerId + '_media_changed'] = true
      }
    }
  }
}
</script>